import React, { useState } from "react";
import { graphql, Link } from 'gatsby';
import { Helmet } from "react-helmet"
import Header from '../components/Header';
import Essay from '../img/essays.svg'
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';


const EssaysPage = ({data}) => {
    const {edges} = data.allMarkdownRemark;
    const allPosts = data.allMarkdownRemark.edges;

    const tags = data.allMarkdownRemark.distinct;
    if(!tags.includes("all")) {
      tags.unshift("all");
    } 
    const defaultOption = tags[0];
    console.log(tags);

    const [state, setState] = useState({
      filteredData: [],
      filter: defaultOption,
    })
    
    const handleFilterChange = event => {
      console.log(event.value)
      const filter = event.value
      const { edges } = data
      const posts = data.allMarkdownRemark.edges || []

      const filteredData = posts.filter(post => {
        const { tag } = post.node.frontmatter
        return (
          (tag.includes(filter.toLowerCase()))
        )
      })
      console.log(filteredData)
      setState({
        filter,
        filteredData,
      })
    }
    const { filteredData, filter } = state
    const hasSearchResults = filteredData && filter !== defaultOption
    const posts = hasSearchResults ? filteredData : allPosts
  

    return (
        <section className="section" style={{"paddingTop": "1rem"}}>
        <Helmet>
            <meta charSet="utf-8" />
            <title>Writings</title>
            </Helmet>
        <div className="container">
        <div className="columns">
            <div className="column is-one-sixth"> </div>
            <div className="column is-three-fifths">  
            <Header />
            <div class="level-item has-text-centered">
              <figure className="image is-128x128 is-inline-block">
                  <img class="is-square" src={Essay} alt="Me"  />
              </figure>
            </div>
            <br /> 
            <h1 className="title"> Writing </h1>
            <br />
            <div style={{display: "flex", justifyContent: "space-between"}}>
            <p style={{marginBottom: "0.5rem"}}><strong className="has-text-grey">All Posts</strong></p>
            <Dropdown style={{border:"none", padding: "1.5rem 1.5rem 1rem 1rem"}} options={tags} onChange={handleFilterChange} value={defaultOption} placeholder="Select an option" />
            </div>

              {posts.map(edge => {
                const {frontmatter} = edge.node;
                return (
                  <div className="box" key={frontmatter.path} style={{color: "grey", marginBottom: "1rem"}}>
                    <span style={{fontSize: "0.75rem"}}>{frontmatter.date} </span> <br />
                    <Link to={frontmatter.path}>
                    <p>{frontmatter.title}</p>
                    </Link>
                    <p style={{fontSize: "0.85rem", marginBottom: "0.5rem", marginTop: "0.1rem"}}>{frontmatter.excerpt}</p>
                  </div>
                )
            })}

        </div>
        <div className="column is-one-sixth"></div>
        </div>
        </div>
    </section>
    );
}

export const query = graphql`
  query BlogQuery {
    allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date]}, filter: {frontmatter: {tag: {eq: "blog_post"}}}) {
      edges {
        node {
          frontmatter {
            title
            excerpt
            path
            date
            tag
          }
        }
      }
      distinct(field: frontmatter___tag)
    }
  }
`

export default EssaysPage
